import React, { useMemo } from "react";

export function ProgressText({ text }: { text: string }): React.ReactNode {
  const renderText = useMemo(() => {
    //  \n ```, 
    //  \n ``` 
    const newText = text.replace(/\n[`]{0,3}$/, "");
    return newText;
  }, [text]);

  return <div className="progress-text">{renderText}</div>;
}
